.nav-color {
  color: white;
}

.logo {
  height: 60px;
}

.objective {
  font-size: 20px;
}

a {
  color: white;
}

a:hover,
a.focus {
  color: white;
  font-weight: 500;
}

.title-row {
  margin-top: 30px;
}

.title {
  font-size: 54px;
  font-weight: 500;
  line-height: 66px;
  font-style: italic;
  color: #004d99;
}

.right {
  font-size: 80px;
  font-weight: 800;
  font-style: normal;
}

.description-row {
  margin-top: 20px;
}

.description {
  font-size: 16px;
  line-height: 20px;
  color: #004d99;
}

.author {
  color: #aa00ff;
}

.film {
  font-size: 30px;
  color: #aa00ff;
}

.video-row {
  margin-top: 30px;
}

.resources-row {
  position: fixed;
  bottom: 50px;
}

.resources {
  font-size: 30px;
  color: #000099;
}

.links {
  color: #000099;
}



